header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ======================= CTA (Call To Action) ======================= */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ======================= HEADER SOCIALS ======================= */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 10rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* ======================= ME ======================= */
.me {
  /* background-color: var(--color-primary); */
  width: 25rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 14rem);
  margin-top: 2rem;
  overflow: hidden;
  padding: 1.5rem 1.5rem 1.5rem 1.5rem;
}

.img__me {
  border-radius: 12rem 12rem 1rem 1rem;
  background: linear-gradient(var(--color-primary), transparent);
}

/* ======================= SCROLL DOWN ======================= */
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 12rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* ======================= MEDIA QUERIES (MEDIUM DEVICES) ======================= */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/* ======================= MEDIA QUERIES (SMALL DEVICES) ======================= */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }

  .header__socials,
  .scroll__down {
    display: none;
  }
}
